<template>
  <div id="home">
    <el-container>
      <!-- 头部区域 -->
      <el-header>
        <div>
          <img src="../assets/Ahlogo.jpg" alt="" width="50" height="50">
          <span class="theme">等保信息管理系统</span>
        </div>
        <el-dropdown trigger="click">
          <span class="el-dropdown-link">
            {{name}}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item icon="el-icon-plus" @click.native="editPwd = true">修改密码</el-dropdown-item>
            <el-dropdown-item icon="el-icon-circle-plus" @click.native="logout">退出</el-dropdown-item>
          </el-dropdown-menu>
          <el-dialog title="修改密码" :visible.sync="editPwd" width="50%"
          @close="editPwdClose">
            <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="90px" :label-position="labelPosition">
              <el-form-item label="密码   :" prop="pass">
                <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="确认密码   :" prop="checkPass">
                <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="resetForm('ruleForm')">重置</el-button>
              <el-button @click="editPwd = false">取 消</el-button>
              <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
            </span>
          </el-dialog>
        </el-dropdown>
      </el-header>
      <!-- 页面主体区域 -->
      <el-container class="home_container">
        <!-- 侧边栏 -->
        <el-aside :width="isCollapse ? '64px' : '200px'">
          <div class="toggle-button" @click="toggleCollapse">|||</div>
          <el-menu background-color="#333744" text-color="#fff" active-text-color="#409EFF"
          unique-opened :collapse="isCollapse" :collapse-transition="false" :router="true"
          :default-active="activePath">
            <el-menu-item :index="item.index" v-for="item in infoList" :key="item.index"
            @click="saveNavState(item.index)">
              <i :class="item.class"></i>
              <span slot="title">{{item.text}}</span>
            </el-menu-item>
         </el-menu>
        </el-aside>
        <!-- 右侧主体内容 -->
        <el-main>
          <!-- 路由占位符 -->
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Cookies from 'cookie-javascript'
import secret from '../secret/secret'
export default {
  data () {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    const pwd = (rule, value, callback) => {
      const pwd = /^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$)([^\u4e00-\u9fa5\s]){6,18}$/
      if (!pwd.test(value)) {
        callback(new Error('请输入6-18位英文字母、数字或者符号（除空格），且字母、数字和标点符号至少包含两种'))
      } else {
        callback()
      }
    }
    return {
      labelPosition: 'left',
      name: '',
      editPwd: false,
      infoList: [
        { index: '/welcome', class: 'iconfont icon-tijikongjian', text: '首页' },
        { index: '/add', class: 'iconfont icon-danju', text: '信息录入' },
        { index: '/check', class: 'iconfont icon-lock_fill', text: '信息审核' },
        { index: '/usermanage', class: 'iconfont icon-users', text: '用户管理' },
        { index: '/apartmanage', class: 'iconfont icon-shangpin', text: '部门管理' },
        { index: '/statistics', class: 'iconfont icon-baobiao', text: '信息统计' }
      ],
      isCollapse: false,
      activePath: '',
      ruleForm: {
        pass: '',
        checkPass: ''
      },
      rules: {
        pass: [
          { validator: validatePass, trigger: 'blur' },
          { validator: pwd, trigger: 'blur' },
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 18, message: '长度在 6 到 18 个字符', trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' },
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 18, message: '长度在 6 到 18 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.activePath = window.sessionStorage.getItem('activePath')
    this.$get('/user/' + window.sessionStorage.getItem('userid'))
      .then(response => {
        if (response.data.meta.status === 200) {
          this.name = response.data.data.name
        }
      })
      .catch(err => {
        this.$message.error(err)
      })
  },
  methods: {
    logout () {
      // 清空token,重定向到login页面
      Cookies.remove('token')
      // console.log(Cookies.get('token'))
      window.sessionStorage.clear()
      window.localStorage.clear()
      this.$router.push('/login')
    },
    toggleCollapse () {
      // 点击按钮,切换菜单的折叠与展开
      this.isCollapse = !this.isCollapse
    },
    saveNavState (activePath) {
      // 保存链接的激活状态
      window.sessionStorage.setItem('activePath', activePath)
    },
    editPwdClose () {
      this.$refs.ruleForm.resetFields()
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.ruleForm.pass = secret.encrypt(this.ruleForm.pass, 'ABGHNJHGSHUYG12')
          this.$put('/user/update/password', { id: window.sessionStorage.getItem('userid'), password: this.ruleForm.pass })
            .then(response => {
              this.$message.success(response.meta.msg)
              this.$router.push('/login')
              Cookies.remove('token')
            })
            .catch(err => {
              this.$message.error(err.meta.msg)
            })
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<style lang="less" scoped>
.theme {
  color: gray;
}
.home_container {
  height: 90vh;
}
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;
  img {
    border-radius: 50%;
  }
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}
.el-container {
  background-color: #373d41;
}
.el-aside {
  background-color: #333744;
  .el-menu {
    border-right: none;
  }
}
.el-main {
  background-color: #eaedf1;
}
.iconfont {
  margin-right: 10px;
}
.toggle-button {
  background-color: #4a5064;
  font-size: 10px;
  line-height: 25px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
a {
  text-decoration: none;
  color: gray;
}
.el-dropdown-link {
  cursor: pointer;
  color: gray;
}
</style>
