<template>
  <div id="usermanage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>部门管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!-- 添加部门 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="请输入内容" v-model="query" clearable @clear="getApartmentData">
            <el-button slot="append" icon="el-icon-search" @click="getSearchData" @keyup.enter.native="getSearchData"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addApartment">添加部门</el-button>
        </el-col>
      </el-row>
      <!-- 部门信息展示 -->
      <el-table :data="apartmentData" style="width: 100%" border>
          <el-table-column prop="id" label="编号"></el-table-column>
          <el-table-column prop="name" label="部门"></el-table-column>
          <el-table-column prop="principal_name" label="负责人"></el-table-column>
          <el-table-column prop="principal_telephone" label="移动电话"></el-table-column>
          <el-table-column prop="principal_office_telephone" label="座机"></el-table-column>
          <el-table-column prop="principal_email" label="邮箱"></el-table-column>
          <el-table-column  label="操作" width="120px">
            <template slot-scope="scope">
              <el-tooltip  effect="dark" content="修改信息" placement="top" :enterable="false">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="updateInfo(scope.row.id)"></el-button>
              </el-tooltip>
              <el-tooltip  effect="dark" content="删除信息" placement="top" :enterable="false">
                <el-button type="info" icon="el-icon-delete" size="mini" @click="deleteInfo(scope.row.id)"></el-button>
              </el-tooltip>
              <!-- 修改信息部分 -->
              <el-dialog title="修改部门" :visible.sync="editApartment" width="50%" @close="editFormClose">
                <el-form ref="editFormRef" :rules="editFormRules" :model="editForm" label-width="130px" :label-position="labelPosition">
                  <el-form-item label="部门名" prop="name">
                    <el-input v-model="editForm.name"></el-input>
                  </el-form-item>
                  <el-form-item label="负责人姓名" prop="principal_name">
                    <el-input v-model="editForm.principal_name"></el-input>
                  </el-form-item>
                  <el-form-item label="负责人手机号" prop="principal_telephone">
                    <el-input v-model="editForm.principal_telephone"></el-input>
                  </el-form-item>
                  <el-form-item label="负责人电子邮箱" prop="principal_email">
                    <el-input v-model="editForm.principal_email"></el-input>
                  </el-form-item>
                  <el-form-item label="负责人职称" prop="principal_title">
                    <el-input v-model="editForm.principal_title"></el-input>
                  </el-form-item>
                  <el-form-item label="负责人办公室电话" prop="principal_office_telephone">
                    <el-input v-model="editForm.principal_office_telephone"></el-input>
                  </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="editApartment = false">取 消</el-button>
                  <el-button type="primary" @click="editApartmentInfo">确 定</el-button>
                </span>
              </el-dialog>
            </template>
          </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
      :current-page="pagenum" :page-sizes="[1, 2, 5, 10]"
      :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper"
      :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    const tel = (rule, value, callback) => {
      const tel = /^[1][3-9][0-9]{9}$/
      if (!tel.test(value)) {
        callback(new Error('请输入正确的电话号码'))
      } else {
        callback()
      }
    }
    const email = (rule, value, callback) => {
      const email = /^([a-zA-Z0-9]+[-_.]?)+@[a-zA-Z0-9]+\.[a-z]+$/
      if (!email.test(value)) {
        callback(new Error('请输入正确的邮箱'))
      } else {
        callback()
      }
    }
    const phone = (rule, value, callback) => {
      const phone = /0\d{2,3}-\d{7,8}/
      if (!phone.test(value)) {
        callback(new Error('请输入正确的固定电话'))
      } else {
        callback()
      }
    }
    const title = (rule, value, callback) => {
      const title = /^[\u0391-\uFFE5A-Za-z]+$/
      if (!title.test(value)) {
        callback(new Error('请输入中文'))
      } else {
        callback()
      }
    }
    return {
      labelPosition: 'left',
      apartmentData: [],
      query: '',
      pagenum: 1,
      pagesize: 2,
      total: 0,
      // 控制修改用户对话框的显示与隐藏
      editApartment: false,
      editForm: {},
      editFormRules: {
        name: [
          { required: true, message: '部门名不能为空!', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        principal_name: [
          { required: true, message: '负责人姓名不能为空!', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
        ],
        principal_telephone: [
          { validator: tel, trigger: 'blur' }
        ],
        principal_email: [
          { validator: email, trigger: 'blur' }
        ],
        principal_office_telephone: [
          { validator: phone, trigger: 'blur' }
        ],
        principal_title: [
          { validator: title, trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    // this.getApartmentData() // 获取所有部门信息
    this.checkAuthority()
    this.getSearchData() // 搜索关键词为空时显示全部用户信息
  },
  methods: {
    addApartment () {
      this.$router.push('/addapartment')
    },
    // 获取所有部门信息
    getApartmentData () {
      this.$get('/department', { pagenum: this.pagenum, pagesize: this.pagesize, query: this.query })
        .then(response => {
          if (response.data.meta.status === 200) {
            this.apartmentData = response.data.data.rows
            this.total = response.data.data.total
          }
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    // 监听 pagesize 改变的事件
    handleSizeChange (newSize) {
      this.pagesize = newSize
      this.getApartmentData()
    },
    // 监听 页码值 改变的事件
    handleCurrentChange (newPage) {
      this.pagenum = newPage
      this.getApartmentData()
    },
    updateInfo (id) {
      this.editApartment = true
      this.$get('/department/findById/' + id, {})
        .then(response => {
          this.editForm = response.data.data
        })
        .catch(err => {
          console.log(err)
        })
    },
    editFormClose () {
      this.$refs.editFormRef.resetFields()
    },
    editApartmentInfo () {
      this.$refs.editFormRef.validate(valid => {
        if (!valid) return
        // 发起修改部门请求
        this.$put('/department/' + this.editForm.id, this.editForm)
          .then(response => {
            if (response.meta.status === 200) {
              this.editApartment = false
              this.$message.success('修改成功')
              this.getSearchData(JSON.parse(window.sessionStorage.getItem('temporaryApartInfo')))
            }
          })
          .catch(err => {
            console.log(err)
            this.$message.error('修改失败')
          })
      })
    },
    async deleteInfo (id) {
      // 弹框询问是否删除
      const confirmResult = await this.$confirm('此操作将永久删除该部门, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已经取消删除')
      }
      this.$delete('/department/' + id, {})
        .then(response => {
          if (response.meta.status === 204) {
            this.$message.success(response.meta.msg)
            this.getSearchData(JSON.parse(window.sessionStorage.getItem('temporaryApartInfo')))
          }
        })
        .catch(err => err)
    },
    getSearchData () {
      this.$get('/department/findByName', { pagenum: this.pagenum, pagesize: this.pagesize, query: this.query })
        .then(response => {
          if (response.data.meta.status === 200) {
            this.apartmentData = response.data.data.rows
            this.total = response.data.data.total
            this.temporaryApartInfo = {
              pagenum: this.pagenum,
              pagesize: this.pagesize,
              query: this.query
            }
            window.sessionStorage.setItem('temporaryApartInfo', JSON.stringify(this.temporaryApartInfo))
          }
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    // 检查用户权限
    checkAuthority () {
      // 根据用户权限显示页面
      if (window.sessionStorage.getItem('userAuthority') === '2') {
        this.$router.push('/home')
        this.$message.error('您没有访问权限')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  .el-table {
    margin-top: 10px;
  }
  .el-pagination {
    margin-top: 10px;
    text-align: center;
  }
}
</style>
