<template>
  <div id="check">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/check' }">信息审核</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table :data="checkData" style="width: 100%" border>
        <el-table-column prop="name" label="系统名"></el-table-column>
        <el-table-column prop="review_state" label="审核状态">
          <template slot-scope="scope">
            <span v-if="scope.row.review_state == 0">已保存</span>
            <span v-if="scope.row.review_state == 1">未审核</span>
            <span v-if="scope.row.review_state == 2">通过</span>
            <span v-if="scope.row.review_state == 3">未通过</span>
          </template>
        </el-table-column>
        <el-table-column  label="操作" width="100px">
          <template slot-scope="scope">
            <el-tooltip  effect="dark" content="修改信息" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="checkInfo(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
      :current-page="pagenum" :page-sizes="[1, 2, 5, 10]"
      :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper"
      :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      checkData: [],
      pagenum: 1,
      pagesize: 2,
      total: 0
    }
  },
  created () {
    this.getSystems()
    this.checkAuthority()
  },
  methods: {
    checkInfo (id) {
      window.sessionStorage.setItem('systemid', id)
      this.$router.push('/checksystem')
      // this.$router.push({ name: 'CheckSystem', query: { id: id } })
    },
    getSystems () {
      this.$get('/sysInfo', { pagenum: this.pagenum, pagesize: this.pagesize })
        .then(response => {
          this.checkData = response.data.data.rows
          this.total = response.data.data.total
        })
    },
    // 监听 pagesize 改变的事件
    handleSizeChange (newSize) {
      this.pagesize = newSize
      this.getSystems()
    },
    // 监听 页码值 改变的事件
    handleCurrentChange (newPage) {
      this.pagenum = newPage
      this.getSystems()
    },
    // 检查用户权限
    checkAuthority () {
      // 根据用户权限显示页面
      if (window.sessionStorage.getItem('userAuthority') === '2') {
        this.$router.push('/home')
        this.$message.error('您没有访问权限')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  .el-table {
    margin-top: 10px;
  }
  .el-button {
    margin-left: 20px;
  }
  .el-pagination {
    margin-top: 10px;
    text-align: center;
  }
}
</style>
