<script>
export default {
  data () {
    return {
      syetemFlag: true,
      levelFlag: false,
      labelPosition: 'left',
      checkXtForm: {},
      checkDingjiForm: {},
      systemRules: {
        name: [
          { required: true, message: '系统名称不能为空!', trigger: 'blur' },
          { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getSystems()
    this.getSystemsLevel()
  },
  methods: {
    // checkSystem
    fowardAdd () {
      this.$router.push('/add')
    },
    nextPageLevel () {
      this.syetemFlag = false
      this.levelFlag = true
    },
    getSystems () {
      this.$get('/sysInfo/findById/' + window.sessionStorage.getItem('editsystemid'))
        .then(response => {
          this.checkXtForm = response.data.data
        })
    },
    // checkLevel
    getSystemsLevel () {
      this.$get('/sysInfo/findById/' + window.sessionStorage.getItem('editsystemid'))
        .then(response => {
          this.checkDingjiForm = response.data.data
        })
    },
    fowardPage () {
      this.syetemFlag = true
      this.levelFlag = false
    },
    commit () {
      const obj = Object.assign(this.checkXtForm, this.checkDingjiForm)
      obj.review_state = 1
      this.$put('/sysInfo/' + window.sessionStorage.getItem('editsystemid'), obj)
        .then(response => {
          this.$message.success(response.meta.msg)
        })
    }
  }
}
</script>

<style lang="less">
