<template>
  <div class="login_container">
    <div class="login_box">
      <!-- 头像区域 -->
      <div class="avatar_box">
        <img src="../assets/Ahlogo.jpg" alt="">
      </div>
      <!-- 登录表单区域 -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
        <!-- 用户名 -->
        <el-form-item prop="id">
          <el-input prefix-icon="iconfont icon-user" v-model="loginForm.id"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input prefix-icon="iconfont icon-3702mima" @keyup.enter.native="validateLoginForm" v-model="loginForm.password" type="password"></el-input>
        </el-form-item>
        <!-- 按钮区域 -->
        <el-form-item class="btns">
          <el-button type="primary" @click="validateLoginForm">登录</el-button>
          <el-button type="info" @click="resetLoginForm" >重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import secret from '../secret/secret'
export default {
  data () {
    const isNum = (rule, value, callback) => {
      const id = /^[0-9]*$/
      if (!id.test(value)) {
        callback(new Error('用户名只能为数字'))
      } else {
        callback()
      }
    }
    const pwd = (rule, value, callback) => {
      const pwd = /^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$)([^\u4e00-\u9fa5\s]){6,18}$/
      if (!pwd.test(value)) {
        callback(new Error('请输入6-18位英文字母、数字或者符号（除空格），且字母、数字和标点符号至少包含两种'))
      } else {
        callback()
      }
    }
    return {
      loginForm: {
        id: '',
        password: ''
      },
      loginFormRules: {
        // 验证用户名
        id: [
          { validator: isNum, trigger: 'blur' },
          { required: true, message: '用户名不能为空!', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        // 验证密码
        password: [
          { required: true, message: '密码不能为空', trigger: 'blur' },
          { validator: pwd, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 表单重置
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    // 登录预验证,在发送登录请求之前先进行用户名与密码的验证
    validateLoginForm () {
      this.$refs.loginFormRef.validate(validate => {
        if (!validate) {
          this.$alert('用户名或密码不合规', '登录失败', {
            confirmButtonText: '确定'
          })
        }
        // 密码加密传输
        this.loginForm.password = secret.encrypt(this.loginForm.password, 'ABGHNJHGSHUYG12')
        // 发送登录请求
        this.$post('/user/login', this.loginForm)
          .then((response) => {
            if (response.data.meta.status === 200) {
              Cookies.set('token', response.data.token)
              // 保存当前登录的用户id
              window.sessionStorage.setItem('userid', this.loginForm.id)
              // 获取用户信息得到用户权限
              this.$get('/user/' + this.loginForm.id)
                .then(response => {
                  // 保存当前用户的权限
                  window.sessionStorage.setItem('userAuthority', response.data.data.authority)
                })
              this.$message.success('登录成功')
              this.$router.push('/welcome')
            } else {
              this.$message.error(response.data.meta.msg)
            }
          })
          .catch(err => err)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  // background-color: #2b4b6b;
  background-image: url('../assets/Ahbg.jpg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 100%;
}
.login_box {
  width:450px;
  height: 300px;
  background-color: #fff;
  // background-color: rgb(221, 253, 252,0.1);
  border-radius: 3px;
  position: absolute;
  left: 70%;
  top: 45%;
  transform: translate(-50%,-50%);
  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid gray;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}
.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
.btns {
  display: flex;
  justify-content: flex-end;
}
</style>
