<template>
  <div id="welcome">
    <!-- 顶部区域 -->
    <el-card shadow="hover">
      <el-row :gutter="20">
        <el-col :xs="15" :sm="13" :md="12" :lg="12" :xl="12">
          <img src="../assets/Avatar.png" alt="">
          <span>欢迎&nbsp;<b style="letter-spacing:1px">{{name}}</b>&nbsp;进入等保信息管理系统</span>
        </el-col>
        <el-col :xs="9" :sm="11" :md="12" :lg="12" :xl="12">
          <div>
            <p>待审核:{{uncheceked}}</p>
            <p>已通过:{{pass}}</p>
          </div>
        </el-col>
      </el-row>
    </el-card>
    <!-- 底部区域 -->
    <el-card>
        <el-container>
          <!-- 统计区域 -->
          <el-aside width="400px">
            <div id="pie" :style="{width: '400px', height: '400px'}"></div>
          </el-aside>
          <!-- 提交信息显示 -->
          <el-main>
            <p>最近提交&nbsp;>></p>
            <div id="parent" class="parent">
              <ul id="child1"  class="child">
                <li v-for='(item, index) in items' :key="index">{{item.importTime}}{{item.departmentName}}{{item.importerName}}上传了{{item.systemName}}的等保信息</li>
              </ul>
              <ul id="child1"  class="child">
                <li v-for='(item, index) in items' :key="index">{{item.importTime}}{{item.departmentName}}{{item.importerName}}上传了{{item.systemName}}的等保信息</li>
              </ul>
              <ul id="child1"  class="child">
                <li v-for='(item, index) in items' :key="index">{{item.importTime}}{{item.departmentName}}{{item.importerName}}上传了{{item.systemName}}的等保信息</li>
              </ul>
            </div>
          </el-main>
        </el-container>
    </el-card>
  </div>
</template>

<script type="text/javascript">
export default {
  data () {
    return {
      name: '',
      uncheceked: '无权限',
      pass: '无权限',
      animate: false,
      items: []
    }
  },
  created () {
    // 获取用户名
    this.$get('/user/' + window.sessionStorage.getItem('userid'))
      .then(response => {
        this.name = response.data.data.name
      })
    // 饼状图
    this.drawPie()
    // 待审核 已通过
    this.$get('/sysInfo/countByAuthority')
      .then(response => {
        this.uncheceked = response.data.data[0]
        this.pass = response.data.data[1]
      })
    // 消息滚动数据
    this.$get('/sysInfo/carouselData')
      .then(response => {
        this.items = response.data.data
      })
    // 消息滚动动画
    this.$nextTick(() => {
      const child1 = document.getElementById('child1')
      // const child2 = document.getElementById('child2')
      const parent = document.getElementById('parent')
      // child2.innerHTML = child1.innerHTML
      // console.log(child2.innerHTML)
      setInterval(() => {
        if (parent.scrollTop >= child1.scrollHeight) {
          parent.scrollTop = 0
        } else {
          parent.scrollTop++
        }
      }, 20)
    })
  },
  methods: {
    // 饼状图
    drawPie () {
      this.$get('/sysInfo/groupGradeNum')
        .then(response => {
          const gradenum = response.data.data
          // 为charts选定父容器并初始化charts画布
          const myChart = this.$echarts.init(document.getElementById('pie'))
          // 为图表添加数据
          myChart.setOption({
            title: {
              text: '集团的各级系统占比',
              left: 'center' // 标题在图表中间
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b} : ({d}%)'
            },
            legend: {
              bottom: 10,
              left: 'center', // 底部注释的位置
              data: ['二级', '三级', '四级']
            },
            series: [
              {
                type: 'pie',
                radius: '65%',
                center: ['50%', '50%'],
                selectedMode: 'single',
                data: [
                  { value: gradenum.grade2num, name: '二级' },
                  { value: gradenum.grade3num, name: '三级' },
                  { value: gradenum.grade4num, name: '四级' }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          })
        })
        .catch(err => err)
    }
  }
}
</script>

<style lang="less" scoped>
.el-card:first-child {
  .el-row {
    position: relative;
    .el-col:first-child {
      span {
        margin-left: 15px;
      }
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        vertical-align: middle;
      }
    }
    .el-col:last-child {
      div {
        position: absolute;
        right: 40px;
        top: -18px;
      }
    }
  }
}
.el-card:last-child {
  .el-aside {
    margin-top: 20px;
  }
  .el-main {
    p {
      margin-top: 0;
      margin-left: 15px;
      color: cadetblue;
    }
    .parent {
      width: 600px;
      height: 380px;
      margin: 0 auto;
      // background: rgba(100, 100, 100, 0.1);
      overflow: hidden;
		}
		/*设置的子盒子高度大于父盒子，产生溢出效果*/
    .child {
      height: auto;
		}
		.child li {
      list-style: none;
			height: auto;
			margin: 12px 10px;
      color: #333;
		}
  }
}
</style>
