<template>
  <div id="check">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/check' }">信息审核</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/checksystem' }">系统信息情况</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/checklevel' }">定级情况</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/checkcommit' }">审核</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-steps :active="3">
        <el-step title="系统信息情况"></el-step>
        <el-step title="定级情况"></el-step>
        <el-step title="审核"></el-step>
      </el-steps>
      <el-form ref="checkTjFormRef" :model="checkTjForm" label-width="90px" :label-position="labelPosition">
        <el-form-item label="审核结果  :" :rules="[{ required: true }]">
          <el-radio-group v-model="checkTjForm.review_state">
            <el-radio :label="2">通过</el-radio>
            <el-radio :label="3">不通过</el-radio>
          </el-radio-group>        </el-form-item>
        <el-form-item label="备注  :">
          <el-input type="textarea" v-model="checkTjForm.desc" class="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div class="box">
        <el-button type="primary" @click="fowardPage">上一页</el-button>
        <el-button type="primary" @click="onSubmit">提交</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      checkTjForm: {},
      labelPosition: 'left'
    }
  },
  methods: {
    fowardPage () {
      this.$router.push('/checklevel')
    },
    onSubmit () {
      this.$put('/sysInfo/review/' + window.sessionStorage.getItem('systemid'), this.checkTjForm)
        .then(response => {
          this.$message.success(response.meta.msg)
        })
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  .el-form {
    margin-top: 20px;
  }
  .btn {
    margin-left: -80px;
  }
  .textarea {
    width: 300px;
  }
  .box {
    text-align: center;
  }
}
</style>
