<template>
  <div id="statistics">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/statistics' }">数据统计</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <br>
      <div id="pillar" :style="{width: '1000px', height: '500px'}"></div>
      <br>
      <div class="box1">
        <div id="pie" :style="{width: '400px', height: '400px'}"></div>
        <div id="entryStatus" :style="{width: '400px', height: '400px'}"></div>
      </div>
      <br>
      <div class="box2">
        <div id="safetyProducts" :style="{width: '400px', height: '400px'}"></div>
        <div id="networkProducts" :style="{width: '400px', height: '400px'}"></div>
      </div>
      <br>
      <div class="box3">
        <div id="operatingSystems" :style="{width: '400px', height: '400px'}"></div>
        <div id="numberOfDatabases" :style="{width: '400px', height: '400px'}"></div>
      </div>
      <div id="numberOfServers" :style="{width: '400px', height: '400px'}"></div>
    </el-card>
  </div>
</template>

<script>
export default {
  created () {

  },
  mounted () {
    // 调用方法绘制图表
    this.drawPie()
    this.drawPillar()
    this.drawEntryStatus()
    this.drawProduct('safety', 'safetyProducts', '国产安全产品数占比', '国产安全产品数', '进口安全产品数')
    this.drawProduct('network', 'networkProducts', '国产网络产品数占比', '国产网络产品数', '进口网络产品数')
    this.drawProduct('database', 'operatingSystems', '国产操作系统数占比', '国产操作系统数', '进口操作系统数')
    this.drawProduct('server', 'numberOfDatabases', '国产数据库数占比', '国产数据库数', '进口数据库数')
    this.drawProduct('operate_sys', 'numberOfServers', '国产服务器数占比', '国产服务器数', '进口服务器数')
  },
  methods: {
    drawPie () {
      this.$get('/sysInfo/groupGradeNum')
        .then(response => {
          const gradenum = response.data.data
          // 为charts选定父容器并初始化charts画布
          const myChart = this.$echarts.init(document.getElementById('pie'))
          // 为图表添加数据
          myChart.setOption({
            title: {
              text: '集团的各级系统占比',
              left: 'center' // 标题在图表中间
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b} : ({d}%)'
            },
            legend: {
              bottom: 10,
              left: 'center', // 底部注释的位置
              data: ['二级', '三级', '四级']
            },
            series: [
              {
                type: 'pie',
                radius: '65%',
                center: ['50%', '50%'],
                selectedMode: 'single',
                data: [
                  { value: gradenum.grade2num, name: '二级' },
                  { value: gradenum.grade3num, name: '三级' },
                  { value: gradenum.grade4num, name: '四级' }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          })
        })
        .catch(err => err)
    },
    drawPillar () {
      this.$get('/sysInfo/allDeptGradeStatus')
        .then(response => {
          const deptNameArr = []
          const grade2numArr = []
          const grade3numArr = []
          const grade4numArr = []
          response.data.data.forEach(element => {
            deptNameArr.push(element.deptName)
            grade2numArr.push(element.grade2num)
            grade3numArr.push(element.grade3num)
            grade4numArr.push(element.grade4num)
          })
          // 为charts选定父容器并初始化charts画布
          const myChart = this.$echarts.init(document.getElementById('pillar'))
          // 为图表添加数据
          myChart.setOption({
            title: {
              text: '各个部门的各级系统数量',
              top: -5,
              left: 'center' // 标题在图表中间
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'shadow'
              }
            },
            legend: {
              top: 25,
              left: 'center',
              data: ['二级', '三级', '四级']
            },
            xAxis: [
              {
                type: 'category',
                data: deptNameArr,
                axisLabel: {
                  interval: 0,
                  rotate: 35 // 代表逆时针旋转45度
                }
              }
            ],
            yAxis: [
              {
                type: 'value'
              }
            ],
            series: [
              {
                name: '二级',
                type: 'bar',
                data: grade2numArr
              },
              {
                name: '三级',
                type: 'bar',
                data: grade3numArr
              },
              {
                name: '四级',
                type: 'bar',
                data: grade4numArr
              }
            ]
          })
        })
    },
    drawEntryStatus () {
      this.$get('/sysInfo/committedNum')
        .then(response => {
          // 为charts选定父容器并初始化charts画布
          const myChart = this.$echarts.init(document.getElementById('entryStatus'))
          // 为图表添加数据
          myChart.setOption({
            title: {
              text: '集团的录入情况',
              left: 'center' // 标题在图表中间
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b} : ({d}%)'
            },
            legend: {
              bottom: 10,
              left: 'center', // 底部注释的位置
              data: ['未提交', '已提交']
            },
            series: [
              {
                type: 'pie',
                radius: '65%',
                center: ['50%', '50%'],
                selectedMode: 'single',
                data: [
                  { value: response.data.data.committed_num, name: '已提交' },
                  // { value: response.data.data.total_dept_num - response.data.data.committed_num, name: '未提交' }
                  { value: response.data.data.total_dept_num, name: '未提交' }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          })
        })
        .catch(err => err)
    },
    drawProduct (type, id, text, val1, val2) {
      this.$get('/sysInfo/domesticNum/', { type: type })
        .then(response => {
          // 为charts选定父容器并初始化charts画布
          const myChart = this.$echarts.init(document.getElementById(id))
          // 为图表添加数据
          myChart.setOption({
            title: {
              text: text,
              left: 'center' // 标题在图表中间
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b} : ({d}%)'
            },
            legend: {
              bottom: 10,
              left: 'center', // 底部注释的位置
              data: [val1, val2]
            },
            series: [
              {
                type: 'pie',
                radius: '65%',
                center: ['50%', '50%'],
                selectedMode: 'single',
                data: [
                  { value: response.data.data.domestic_num, name: val1 },
                  { value: response.data.data.import_num, name: val2 }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          })
        })
        .catch(err => err)
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  .box1, .box2, .box3{
    display: flex;
    justify-content: center;
    .div {
      flex: 1;
    }
  }
  #numberOfServers {
    margin: 0 auto;
  }
  #pillar {
    margin: 0 auto;
  }
}
</style>
