import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login.vue'
import Home from '../components/Home.vue'
import Welcome from '../components/Welcome.vue'
import Add from '../components/Add.vue'
import Check from '../components/check/Check.vue'
import CheckSystem from '../components/check/CheckSystem.vue'
import CheckLevel from '../components/check/CheckLevel.vue'
import CheckCommit from '../components/check/CheckCommit.vue'
import Usermanage from '../components/Usermanage.vue'
import Apartmanage from '../components/Apartmanage.vue'
import Statistics from '../components/Statistics.vue'
// import InfoUnit from '../components/info/InfoUnit.vue'
// import InfoSystem from '../components/info/InfoSystem.vue'
// import InfoLevel from '../components/info/InfoLevel.vue'
import Info from '../components/info/Info.vue'
import AddUser from '../components/AddUser.vue'
import AddApartment from '../components/AddApartment.vue'
import Cookies from 'cookie-javascript'
// import EditSystem from '../components/edit/EditSystem.vue'
// import EditLevel from '../components/edit/EditLevel.vue'
import Edit from '../components/edit/Edit.vue'

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    { path: '/', redirect: '/login' },
    { path: '/login', component: Login },
    {
      path: '/home',
      component: Home,
      redirect: '/welcome',
      children: [
        { path: '/welcome', component: Welcome },
        { path: '/add', component: Add },
        // { path: '/infounit', component: InfoUnit },
        // { path: '/infosystem', component: InfoSystem },
        // { path: '/infolevel', component: InfoLevel },
        { path: '/info', component: Info },
        { path: '/check', component: Check },
        { name: 'CheckSystem', path: '/checksystem', component: CheckSystem },
        { path: '/checklevel', component: CheckLevel },
        { path: '/checkcommit', component: CheckCommit },
        // { name: 'EditSystem', path: '/editsystem', component: EditSystem },
        // { path: '/editlevel', component: EditLevel },
        { path: '/edit', component: Edit },
        { path: '/usermanage', component: Usermanage },
        { path: '/adduser', component: AddUser },
        { path: '/apartmanage', component: Apartmanage },
        { path: '/addapartment', component: AddApartment },
        { path: '/statistics', component: Statistics }
      ]
    }
  ]
})

// 挂载路由导航守卫
router.beforeEach((to, from, next) => {
  // to 将要访问的路径
  // from 代表从哪个路径跳转而来
  // next 是一个函数，表示放行
  // next()直接放行 next('/login')强制跳转
  if (to.path === '/login') {
    next()
  } else {
    const tokenStr = Cookies.get('token')
    if (JSON.stringify(tokenStr) === '{}') {
      next('/login')
    } else {
      next()
    }
  }
})

export default router
