<template>
  <div id="adduser">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/apartmanage' }">部门管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/addapartment' }">添加部门</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-form ref="addApartFormRef" :model="addApartForm" :rules="addapartRules" label-width="130px" :label-position="labelPosition">
        <el-form-item label="部门名" prop="name">
          <el-input v-model="addApartForm.name"></el-input>
        </el-form-item>
        <el-form-item label="负责人姓名" prop="principal_name">
          <el-input v-model="addApartForm.principal_name"></el-input>
        </el-form-item>
        <el-form-item label="负责人手机号" prop="principal_telephone">
          <el-input v-model="addApartForm.principal_telephone"></el-input>
        </el-form-item>
        <el-form-item label="负责人电子邮箱" prop="principal_email">
          <el-input v-model="addApartForm.principal_email"></el-input>
        </el-form-item>
        <el-form-item label="负责人职称" prop="principal_title">
          <el-input v-model="addApartForm.principal_title"></el-input>
        </el-form-item>
        <el-form-item label="负责人办公室电话" prop="principal_office_telephone">
          <el-input v-model="addApartForm.principal_office_telephone"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="commit">保存</el-button>
          <el-button type="info" @click="reset">重置</el-button>
          <el-button type="warning" @click="cancel">取消</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    const tel = (rule, value, callback) => {
      const tel = /^[1][3-9][0-9]{9}$/
      if (!tel.test(value)) {
        callback(new Error('请输入正确的电话号码'))
      } else {
        callback()
      }
    }
    const email = (rule, value, callback) => {
      const email = /^([a-zA-Z0-9]+[-_.]?)+@[a-zA-Z0-9]+\.[a-z]+$/
      if (!email.test(value)) {
        callback(new Error('请输入正确的邮箱'))
      } else {
        callback()
      }
    }
    const phone = (rule, value, callback) => {
      const phone = /0\d{2,3}-\d{7,8}/ // 例如0731-88888888
      if (!phone.test(value)) {
        callback(new Error('请输入正确的固定电话'))
      } else {
        callback()
      }
    }
    const title = (rule, value, callback) => {
      const title = /^[\u0391-\uFFE5A-Za-z]+$/
      if (!title.test(value)) {
        callback(new Error('请输入中文'))
      } else {
        callback()
      }
    }
    return {
      labelPosition: 'left',
      addApartForm: {
        name: '',
        principal_name: '',
        principal_telephone: '',
        principal_emailprincipal_email: '',
        principal_title: '',
        principal_office_telephone: ''
      },
      addapartRules: {
        name: [
          { required: true, message: '部门名不能为空!', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        principal_name: [
          { required: true, message: '负责人姓名不能为空!', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        principal_telephone: [
          { validator: tel, trigger: 'blur' }
        ],
        principal_email: [
          { validator: email, trigger: 'blur' }
        ],
        principal_office_telephone: [
          { validator: phone, trigger: 'blur' }
        ],
        principal_title: [
          { validator: title, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    cancel () {
      this.$router.push('/apartmanage')
    },
    reset () {
      // 重置表单
      this.$refs.addApartFormRef.resetFields()
    },
    commit () {
      this.$refs.addApartFormRef.validate(valid => {
        if (!valid) return
        // 发起post请求
        this.$post('/department', this.addApartForm)
          .then((response) => {
            console.log(response)
            if (response.data.meta.status === 201) {
              this.$message.success('添加部门成功')
            }
          })
          .catch(err => {
            console.log(err)
            this.$message.error('添加部门失败')
          })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 10px;
  .el-input {
    width: 300px;
  }
  .el-button:nth-child(2) {
    margin-left: 42px;
  }
  .el-button:nth-child(3) {
    margin-left: 42px;
  }
}
</style>
