<template>
  <div id="adduser">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/usermanage' }">用户管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/adduser' }">添加用户</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-form ref="adduserFormRef" :rules="adduserRules" :model="adduserForm" label-width="80px" :label-position="labelPosition">
        <el-form-item label="用户名" prop="name">
          <el-input v-model="adduserForm.name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="adduserForm.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="adduserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="telephone">
          <el-input v-model="adduserForm.telephone"></el-input>
        </el-form-item>
        <el-form-item label="权限">
          <el-select v-model="adduserForm.authority" placeholder="请选择权限">
            <el-option label="超级管理员" value="0"></el-option>
            <el-option label="部门管理员" value="1"></el-option>
            <el-option label="录入员" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属部门" prop="department.id" :rules="[{ required: true, message: '部门不能为空', trigger: 'blur' }]">
          <el-select v-model="adduserForm.department.id" placeholder="请选择部门">
            <el-option  :label="item.name" :value="item.id" v-for="item in apartList" :key="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="commit">保存</el-button>
          <el-button type="primary" @click="reset">重置</el-button>
          <el-button @click="cancel">取消</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    const pwd = (rule, value, callback) => {
      const pwd = /^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$)([^\u4e00-\u9fa5\s]){6,18}$/
      if (!pwd.test(value)) {
        callback(new Error('请输入6-18位英文字母、数字或者符号（除空格），且字母、数字和标点符号至少包含两种'))
      } else {
        callback()
      }
    }
    const email = (rule, value, callback) => {
      const email = /^([a-zA-Z0-9]+[-_.]?)+@[a-zA-Z0-9]+\.[a-z]+$/
      if (!email.test(value)) {
        callback(new Error('请输入正确的邮箱'))
      } else {
        callback()
      }
    }
    const tel = (rule, value, callback) => {
      const tel = /^[1][3-9][0-9]{9}$/
      if (!tel.test(value)) {
        callback(new Error('请输入正确的电话号码'))
      } else {
        callback()
      }
    }
    return {
      labelPosition: 'left',
      adduserForm: {
        name: '',
        password: '',
        email: '',
        telephone: '',
        authority: '2',
        department: {
          id: ''
        }
      },
      apartList: [],
      adduserRules: {
        name: [
          { required: true, message: '用户名不能为空!', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '密码不能为空', trigger: 'blur' },
          { validator: pwd, trigger: 'blur' }
        ],
        email: [
          { validator: email, trigger: 'blur' }
        ],
        telephone: [
          { validator: tel, trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getApartList()
  },
  methods: {
    reset () {
      this.$refs.adduserFormRef.resetFields()
    },
    cancel () {
      this.$router.push('/usermanage')
    },
    commit () {
      console.log('commit...')
      console.log(this.adduserForm.department)
      this.$post('/user', this.adduserForm)
        .then((response) => {
          if (response.data.meta.status === 201) {
            this.$message.success('添加用户成功')
            this.$router.push('/usermanage')
          }
        })
        .catch(err => err)
    },
    getApartList () {
      this.$get('/department/getAll', {})
        .then((response) => {
          if (response.data.meta.status === 200) {
            console.log(response.data.data)
            this.apartList = response.data.data
          }
        })
        .catch(err => err)
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 10px;
  .el-input {
    width: 300px;
  }
}
</style>
