<template>
  <div id="add">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/add' }">信息录入</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="box">
      <!-- 查询区域 -->
      <el-card>
        <el-form ref="searchInputRef" :inline="true" :model="searchInput" label-width="90px" :label-position="labelPosition">
          <el-form-item label="等保等级:" prop="grade_protect_level">
            <el-input  size="small" v-model="searchInput.grade_protect_level"></el-input>
          </el-form-item>
          <el-form-item label="审核时间:" prop="review_time">
            <el-date-picker v-model="searchInput.review_time" type="daterange" align="right" unlink-panels size="small"
              range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="提交时间:" prop="import_time">
            <el-date-picker v-model="searchInput.import_time" type="daterange" align="right" unlink-panels size="small"
              range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="系统关键字:" prop="keywords">
            <el-input v-model="searchInput.keywords" size="small"></el-input>
          </el-form-item>
        </el-form>
        <div class="box">
          <el-button type="primary"  @click="searchInfo">查询</el-button>
          <el-button type="info"  @click="resetLoginForm">重置</el-button>
        </div>
      </el-card>
      <!-- 显示区域 -->
      <el-card>
        <el-row :gutter="60">
          <el-col :xs="{span:24,offset:0}" :sm="{span:7,offset:0}" :md="{span:2,offset:1}">
            <el-button type="primary" @click="addInfo">新增信息</el-button>
          </el-col>
          <el-col :xs="{span:24,offset:0}" :sm="{span:7,offset:0}" :md="{span:2,offset:1}">
            <el-upload
              class="upload-demo"
              ref="upload"
              action="#"
              :limit="3"
              :http-request="uploadOk"
              :before-upload="beforeAvatarUpload"
              :auto-upload="true">
              <el-button slot="trigger" size="large" type="primary">选取文件</el-button>
            </el-upload>
          </el-col>
          <el-col :xs="{span:24,offset:0}" :sm="{span:7,offset:0}" :md="{span:2,offset:1}">
            <el-button type="primary" @click="handleDownload">下载导入模板</el-button>
          </el-col>
        </el-row>
        <el-table :data="systemData" style="width: 100%" border>
          <el-table-column prop="name" label="系统名"></el-table-column>
          <el-table-column prop="department" label="部门名"></el-table-column>
          <el-table-column prop="reviewer" label="审核员"></el-table-column>
          <el-table-column prop="review_state" label="审核状态">
            <template slot-scope="scope">
              <span v-if="scope.row.review_state == 0">已保存</span>
              <span v-if="scope.row.review_state == 1">未审核</span>
              <span v-if="scope.row.review_state == 2">通过</span>
              <span v-if="scope.row.review_state == 3">未通过</span>
            </template>
          </el-table-column>
          <el-table-column prop="pass_time" label="审核时间"></el-table-column>
          <el-table-column  label="操作" width="120px">
            <template slot-scope="scope">
              <el-tooltip  effect="dark" content="修改信息" placement="top" :enterable="false">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="editSystemInfo(scope.row.id)"></el-button>
              </el-tooltip>
               <el-tooltip  effect="dark" content="删除信息" placement="top" :enterable="false">
                <el-button type="info" icon="el-icon-delete" size="mini" @click="deleteSystemInfo(scope.row.id)"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页区域 -->
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="pagenum" :page-sizes="[1, 2, 5, 10]"
          :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      labelPosition: 'left',
      // 搜索框的值
      searchInput: {
        grade_protect_level: '',
        review_time: [],
        import_time: [],
        keywords: ''
      },
      // 临时保存搜索条件
      temporarySearch: {
        grade_protect_level: '',
        review_time: [],
        import_time: [],
        keywords: ''
      },
      systemData: [], // 表格处的数据展示
      pagenum: 1,
      pagesize: 2,
      total: 0
    }
  },
  created () {
    this.searchInfo()
    // 如果有历史搜索记录了,从编辑界面返回的时候就显示根据条件搜索得到的数据
    if (JSON.parse(window.sessionStorage.getItem('temporarySearch'))) {
      this.searchInput.grade_protect_level = JSON.parse(window.sessionStorage.getItem('temporarySearch')).grade
      this.$get('/sysInfo/findByInfo', JSON.parse(window.sessionStorage.getItem('temporarySearch')))
        .then(response => {
          this.systemData = response.data.data.rows
          this.total = response.data.data.total
        })
    }
  },
  methods: {
    // 根据关键词查询系统信息
    searchInfo () {
      this.$get('/sysInfo/findByInfo', {
        pagenum: this.pagenum,
        pagesize: this.pagesize,
        grade: this.searchInput.grade_protect_level,
        review_time: this.searchInput.review_time,
        import_time: this.searchInput.import_time,
        keywords: this.searchInput.keywords
      })
        .then(response => {
          this.systemData = response.data.data.rows
          this.total = response.data.data.total
          this.temporarySearch = {
            pagenum: this.pagenum,
            pagesize: this.pagesize,
            grade: this.searchInput.grade_protect_level,
            review_time: this.searchInput.review_time,
            import_time: this.searchInput.import_time,
            keywords: this.searchInput.keywords
          }
          window.sessionStorage.setItem('temporarySearch', JSON.stringify(this.temporarySearch))
        })
    },
    // 查询表单重置
    resetLoginForm () {
      window.sessionStorage.removeItem('temporarySearch')
      this.searchInput = {}
      this.systemData = ''
      this.$refs.searchInputRef.resetFields()
    },
    // 新增信息
    addInfo () {
      // this.$router.push('/infounit')
      this.$router.push('/info')
    },
    // 修改系统信息
    editSystemInfo (id) {
      window.sessionStorage.setItem('editsystemid', id)
      this.$router.push('/edit')
      this.searchInfo(window.sessionStorage.getItem('temporarySearch'))
    },
    // 删除系统信息
    async deleteSystemInfo (id) {
      // 弹框询问是否删除
      const confirmResult = await this.$confirm('此操作将永久删除该系统, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已经取消删除')
      }
      this.$delete('/sysInfo/' + id, {})
        .then(response => {
          if (response.meta.status === 204) {
            this.$message.success(response.meta.msg)
            // 删除之后再次请求搜索的内容(不包含删除内容的最新搜索内容)
            this.$get('/sysInfo/findByInfo', JSON.parse(window.sessionStorage.getItem('temporarySearch')))
              .then(response => {
                this.systemData = response.data.data.rows
                this.total = response.data.data.total
                this.searchInfo(window.sessionStorage.getItem('temporarySearch'))
              })
          }
        })
        .catch(err => err)
    },
    // 监听 pagesize 改变的事件
    handleSizeChange (newSize) {
      this.pagesize = newSize
      this.searchInfo()
    },
    // 监听 页码值 改变的事件
    handleCurrentChange (newPage) {
      this.pagenum = newPage
      this.searchInfo()
    },
    // 下载Excel模板
    handleDownload () {
      axios({
        method: 'get',
        url: 'http://10.11.37.162:8080/grade_protection/sysInfo/getExcel',
        responseType: 'blob'
      })
        .then((res) => {
          const url = window.URL.createObjectURL(new Blob([res.data]))
          const link = document.createElement('a')
          link.style.display = 'none'
          link.href = url
          link.setAttribute('download', this.$route.meta.title + '.xlsx')
          document.body.appendChild(link)
          link.click()
        })
        .catch(() => {
          this.$message.error('网络错误!')
        })
    },
    // 上传Excel
    // 上传前验证
    beforeAvatarUpload (file) {
      const Xls = file.name.split('.')
      const ls = file.name.split('.')
      if (Xls[1] === 'xls' || ls[1] === 'xlsx') {
        // return file
        console.log(file)
      } else {
        this.$message.error('上传文件只能是 xls/xlsx 格式!')
        return false
      }
    },
    // 上传成功
    uploadOk (val) {
      const fd = new FormData()
      fd.append('file', val.file) // 'file是与后端接收对应的'
      axios({
        method: 'post',
        url: 'http://112.124.46.193:8080/grade_protection/sysInfo/excel',
        headers: { 'Content-Type': 'multipart/form-data; boundary=----WebKitFormBoundaryXUIhYHlAmsiYAKUG' },
        data: fd
      })
        .then(response => {
          console.log(response)
          if (response.data.meta.status === 201) {
            this.$message.success(response.data.meta.msg)
          } else {
            console.log(response)
            this.$message.error('必须包含系统名称和合规的部门名称！')
          }
        })
    }
  }
}
</script>

<style lang="less" scoped>
.box {
  margin-top: 15px;
  .el-card:first-child {
    text-align: center;
    .el-input {
      width: 350px;
    }
    .box {
      text-align: center;
    }
  }
  .el-card:last-child {
    margin-top: 10px;
    .el-table {
      margin-top: 10px;
    }
    .el-pagination {
      margin-top: 10px;
      text-align: center;
    }
  }
}
</style>
