import axios from 'axios'
import Cookies from 'cookie-javascript'
// axios.defaults.timeout = 5000
axios.defaults.baseURL = 'http://112.124.46.193:8080/grade_protection'
// axios.defaults.baseURL = 'http://10.11.37.162:8080/grade_protection'

// http request 拦截器
axios.interceptors.request.use(
  config => {
    config.headers.Authorization = Cookies.get('token')
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

