<template>
  <div id="usermanage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!-- 添加用户 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="请输入内容" v-model="query" clearable @clear="getUserData">
            <el-button slot="append" icon="el-icon-search" @click="getSearchData" @keyup.enter.native="getSearchData"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addUser">添加用户</el-button>
        </el-col>
      </el-row>
      <!-- 用户信息展示 -->
      <el-table :data="userData" style="width: 100%" border>
          <el-table-column prop="id" label="编号"></el-table-column>
          <el-table-column prop="department.name" label="所属部门"></el-table-column>
          <el-table-column prop="name" label="用户名"></el-table-column>
          <el-table-column prop="telephone" label="电话号码"></el-table-column>
          <el-table-column prop="email" label="邮箱"></el-table-column>
          <el-table-column prop="authority" label="权限">
            <template slot-scope="scope">
              <span v-if="scope.row.authority == 0">超级管理员</span>
              <span v-if="scope.row.authority == 1">部门管理员</span>
              <span v-if="scope.row.authority == 2">导入员</span>
            </template>
          </el-table-column>
          <el-table-column  label="操作" width="180px">
            <template slot-scope="scope">
              <el-tooltip  effect="dark" content="修改信息" placement="top" :enterable="false">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="updateInfo(scope.row.id)"></el-button>
              </el-tooltip>
               <el-tooltip  effect="dark" content="删除信息" placement="top" :enterable="false">
                <el-button type="info" icon="el-icon-delete" size="mini" @click="deleteInfo(scope.row.id)"></el-button>
              </el-tooltip>
              <el-tooltip  effect="dark" content="修改密码" placement="top" :enterable="false">
                <el-button type="warning" icon="el-icon-setting" size="mini" @click="editPassword(scope.row.id)"></el-button>
              </el-tooltip>
              <el-dialog title="修改用户" :visible.sync="editUser" width="30%" @close="editFormClose">
                <el-form ref="editFormRef" :rules="editFormRules" :model="editForm" label-width="80px" :label-position="labelPosition">
                  <el-form-item label="用户名" prop="name">
                    <el-input v-model="editForm.name"></el-input>
                  </el-form-item>
                  <el-form-item label="邮箱" prop="email">
                    <el-input v-model="editForm.email"></el-input>
                  </el-form-item>
                  <el-form-item label="手机号" prop="telephone">
                    <el-input v-model="editForm.telephone"></el-input>
                  </el-form-item>
                  <el-form-item label="权限">
                    <el-select v-model="editForm.authority" placeholder="请选择权限">
                      <el-option label="超级管理员" :value="0"></el-option>
                      <el-option label="部门管理员" :value="1"></el-option>
                      <el-option label="录入员" :value="2"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="所属部门" prop="department">
                    <el-select v-model="editForm.department.id" placeholder="请选择部门">
                      <el-option  :value="item.id" :label="item.name"  v-for="item in apartList" :key="item.id"></el-option>
                    </el-select>
                  </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="editUser = false">取 消</el-button>
                  <el-button type="primary" @click="editUserInfo">确 定</el-button>
                </span>
              </el-dialog>
              <el-dialog title="修改密码" :visible.sync="editPwd" width="50%" @close="editPwdClose">
                <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="90px" :label-position="labelPosition">
                  <el-form-item label="密码   :" prop="pass">
                    <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="确认密码   :" prop="checkPass">
                    <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
                  </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="resetForm('ruleForm')">重置</el-button>
                  <el-button @click="editPwd = false">取 消</el-button>
                  <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
                </span>
              </el-dialog>
            </template>
          </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
      :current-page="pagenum" :page-sizes="[1, 2, 5, 10]"
      :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper"
      :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import secret from '../secret/secret'
export default {
  data () {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    const email = (rule, value, callback) => {
      const email = /^([a-zA-Z0-9]+[-_.]?)+@[a-zA-Z0-9]+\.[a-z]+$/
      if (!email.test(value)) {
        callback(new Error('请输入正确的邮箱'))
      } else {
        callback()
      }
    }
    const tel = (rule, value, callback) => {
      const tel = /^[1][3-9][0-9]{9}$/
      if (!tel.test(value)) {
        callback(new Error('请输入正确的电话号码'))
      } else {
        callback()
      }
    }
    return {
      labelPosition: 'left',
      userData: [],
      query: '',
      pagenum: 1,
      pagesize: 2,
      total: 0,
      editUser: false,
      editPwd: false,
      editForm: {
        name: '',
        email: '',
        telephone: '',
        authority: '',
        department: {
          id: ''
        }
      },
      apartList: [],
      ruleForm: {
        pass: '',
        checkPass: ''
      },
      rules: {
        pass: [
          { validator: validatePass, trigger: 'blur' },
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 18, message: '长度在 6 到 18 个字符', trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' },
          { min: 6, max: 18, message: '长度在 6 到 18 个字符', trigger: 'blur' }
        ]
      },
      editFormRules: {
        name: [
          { required: true, message: '用户名不能为空!', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        email: [
          { validator: email, trigger: 'blur' }
        ],
        telephone: [
          { validator: tel, trigger: 'blur' }
        ],
        department: [
          { required: true, message: '所属部门不能为空!', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.checkAuthority()
    // this.getUserData() // 获取所有的用户
    this.getApartList() // 获取所有的部门
    this.getSearchData() // 搜索关键词为空时显示全部用户信息
  },
  methods: {
    // 添加用户
    addUser () {
      this.$router.push('/adduser')
    },
    // 获取用户数据(不需要了)
    getUserData () {
      this.$get('/user', { pagenum: this.pagenum, pagesize: this.pagesize })
        .then(response => {
          if (response.data.meta.status === 200) {
            this.userData = response.data.data.rows
            this.total = response.data.data.total
          }
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    // 监听 pagesize 改变的事件
    handleSizeChange (newSize) {
      this.pagesize = newSize
      this.getUserData()
    },
    // 监听 页码值 改变的事件
    handleCurrentChange (newPage) {
      this.pagenum = newPage
      this.getUserData()
    },
    updateInfo (id) {
      this.editUser = true
      this.$get('/user/' + id)
        .then(response => {
          this.editForm = response.data.data
          this.editForm.department.id = response.data.data.department.id
        })
        .catch(err => {
          this.$message.error(err.data.meta.msg)
        })
    },
    // 修改信息组件关闭后信息重置
    editFormClose () {
      this.$refs.editFormRef.resetFields()
    },
    // 修改密码组件关闭后信息重置
    editPwdClose () {
      this.$refs.ruleForm.resetFields()
    },
    editPassword (id) {
      window.sessionStorage.setItem('curUserid', id)
      this.editPwd = true
    },
    editUserInfo () {
      this.$refs.editFormRef.validate(valid => {
        if (!valid) return
        // 发起修改用户请求
        this.$put('/user/update', this.editForm)
          .then((response) => {
            if (response.meta.status === 200) {
              this.editUser = false
              this.$message.success('修改用户成功')
              this.getSearchData(JSON.parse(window.sessionStorage.getItem('temporaryUserInfo')))
            }
          })
          .catch(err => {
            this.$message.error(err)
          })
      })
    },
    // 获取所有部门
    getApartList () {
      this.$get('/department/getAll', {})
        .then((response) => {
          if (response.data.meta.status === 200) {
            this.apartList = response.data.data
          }
        })
        .catch(err => err)
    },
    async deleteInfo (id) {
      // 弹框询问是否删除
      const confirmResult = await this.$confirm('此操作将永久删除该部门, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .catch(err => err)
      if (confirmResult !== 'confirm') {
        this.$message.info('已经取消删除')
      }
      this.$del('' + id, {})
        .then(response => {
          if (response.data.meta.status === 204) {
            this.$message.success(response.data.meta.msg)
            this.getSearchData(JSON.parse(window.sessionStorage.getItem('temporaryUserInfo')))
          }
        })
        .catch(err => err)
    },
    // 根据关键字搜索所有的用户信息
    getSearchData () {
      this.$get('/user/name', { keyword: this.query, pagenum: this.pagenum, pagesize: this.pagesize, query: this.query })
        .then(response => {
          if (response.data.meta.status === 200) {
            this.userData = response.data.data.rows
            this.total = response.data.data.total
            this.temporaryUserInfo = {
              keyword: this.query,
              pagenum: this.pagenum,
              pagesize: this.pagesize,
              query: this.query
            }
            // 保存当前的搜索条件
            window.sessionStorage.setItem('temporaryUserInfo', JSON.stringify(this.temporaryUserInfo))
          }
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    // 更改密码
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.ruleForm.pass = secret.encrypt(this.ruleForm.pass, 'ABGHNJHGSHUYG12')
          this.$put('/user/update/password', { id: window.sessionStorage.getItem('curUserid'), password: this.ruleForm.pass })
            .then(response => {
              this.editPwd = false
              this.$message.success(response.meta.msg)
            })
            .catch(err => {
              this.$message.error(err.meta.msg)
            })
        }
      })
    },
    // 重置修改信息表单
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 检查用户权限
    checkAuthority () {
      // 根据用户权限显示页面
      if (window.sessionStorage.getItem('userAuthority') === '2') {
        this.$router.push('/home')
        this.$message.error('您没有访问权限')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  .el-table {
    margin-top: 10px;
  }
  .el-pagination {
    margin-top: 10px;
    text-align: center;
  }
}
</style>
