<template>
  <div id="check">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/check' }">信息审核</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/checksystem' }">系统信息情况</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-steps :active="1">
        <el-step title="系统信息情况"></el-step>
        <el-step title="定级情况"></el-step>
        <el-step title="审核"></el-step>
      </el-steps>
      <el-form ref="checkXtFormRef" :model="checkXtForm" label-width="130px" disabled :label-position="labelPosition">
        <el-form-item label="系统名称   :" :rules="[{ required: true, message: '部门不能为空', trigger: 'blur' }]">
          <el-input v-model="checkXtForm.name"></el-input>
        </el-form-item>
        <el-form-item label="等保级别   :">
          <el-select v-model="checkXtForm.grade_protect_level"  placeholder="请选择等保等级">
            <el-option label="2" value="2"></el-option>
            <el-option label="3" value="3"></el-option>
            <el-option label="4" value="4"></el-option>
            <el-option label="5" value="5"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务类型   :">
          <el-input v-model="checkXtForm.business_type"></el-input>
        </el-form-item>
        <el-form-item label="业务描述   :">
          <el-input v-model="checkXtForm.business_desc"></el-input>
        </el-form-item>
        <el-form-item label="服务范围   :">
          <el-input v-model="checkXtForm.service_area"></el-input>
        </el-form-item>
        <el-form-item label="服务对象   :">
          <el-input v-model="checkXtForm.service_obj"></el-input>
        </el-form-item>
          <el-form-item label="网络性质   :">
          <el-input v-model="checkXtForm.network"></el-input>
        </el-form-item>
        <el-form-item label="系统互联情况   :">
          <el-input v-model="checkXtForm.sys_interconnect"></el-input>
        </el-form-item>
        <el-form-item label="安全产品数   :">
          <el-input v-model="checkXtForm.safety_num"></el-input>
        </el-form-item>
        <el-form-item label="国产安全产品数   :">
          <el-input v-model="checkXtForm.domestic_safety_num"></el-input>
        </el-form-item>
        <el-form-item label="网络产品数   :">
          <el-input v-model="checkXtForm.network_num"></el-input>
        </el-form-item>
        <el-form-item label="国产网络产品数   :">
          <el-input v-model="checkXtForm.domestic_network_num"></el-input>
        </el-form-item>
        <el-form-item label="操作系统数   :">
          <el-input v-model="checkXtForm.operate_sys_num"></el-input>
        </el-form-item>
        <el-form-item label="国产操作系统数   :">
          <el-input v-model="checkXtForm.domestic_operate_sys_num"></el-input>
        </el-form-item>
        <el-form-item label="数据库数   :">
          <el-input v-model="checkXtForm.database_num"></el-input>
        </el-form-item>
        <el-form-item label="国产数据库数   :">
          <el-input v-model="checkXtForm.domestic_database_num"></el-input>
        </el-form-item>
        <el-form-item label="服务器数   :">
          <el-input v-model="checkXtForm.server_num"></el-input>
        </el-form-item>
        <el-form-item label="国产服务器数   :">
          <el-input v-model="checkXtForm.domestic_server_num"></el-input>
        </el-form-item>

        <el-form-item label="等级测评   :">
          <el-radio-group v-model="checkXtForm.rank_evaluation">
            <el-radio  :label="true">有</el-radio>
            <el-radio  :label="false">无</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="风险评估">
          <el-radio-group v-model="checkXtForm.risk_assessment">
            <el-radio  :label="true" >有</el-radio>
            <el-radio  :label="false">无</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="灾难恢复">
          <el-radio-group v-model="checkXtForm.disaster_recover">
            <el-radio  :label="true" >有</el-radio>
            <el-radio  :label="false">无</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="应急响应">
          <el-radio-group v-model="checkXtForm.emergency_response">
            <el-radio  :label="true">有</el-radio>
            <el-radio  :label="false">无</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="系统集成   :">
          <el-radio-group v-model="checkXtForm.sys_integration">
            <el-radio  :label="true">有</el-radio>
            <el-radio  :label="false">无</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="安全咨询   :">
          <el-radio-group v-model="checkXtForm.security_advisory">
            <el-radio  :label="true">有</el-radio>
            <el-radio  :label="false">无</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="安全培训">
          <el-radio-group v-model="checkXtForm.security_train">
            <el-radio  :label="true">有</el-radio>
            <el-radio  :label="false">无</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="等级测评单位名称:">
          <el-input v-model="checkXtForm.evaluate_firm_name"></el-input>
        </el-form-item>
        <el-form-item label="投入使用时间   :">
          <el-date-picker v-model="checkXtForm.put_into_time" type="date" placeholder="选择日期"></el-date-picker>
        </el-form-item>
      </el-form>
      <div class="box">
        <el-button type="primary" @click="fowardPage" >上一页</el-button>
        <el-button type="primary" @click="nextPage">下一页</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      labelPosition: 'left',
      checkXtForm: {}
    }
  },
  created () {
    this.getSystems()
  },
  methods: {
    fowardPage () {
      this.$router.push('/check')
    },
    nextPage () {
      this.$router.push('/checklevel')
    },
    getSystems () {
      this.$get('/sysInfo/findById/' + window.sessionStorage.getItem('systemid'))
        .then(response => {
          this.checkXtForm = response.data.data
        })
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  .el-form {
    margin-top: 10px;
  }
  .el-input {
    width: 300px;
  }
  .box {
    text-align: center;
  }
}
</style>
